<template>
  <div class="article-md" style="z-index: 3" v-if="isShow">
    <div class="v-modal" ></div>
    <div class="article-model text-center ">
      <div class="title f44">
        <span v-if="coinParams.type === 'video'"></span>
        <span v-else-if="coinParams.type === 'svideo'"></span>
        <span v-else></span>
      </div>
      <div class="cten">
        <div class="img">
          <img src="../../assets/img/goldBG.png" alt="">
          <div class="f36">
            <div>{{coinParams.coin + coinParams.extra_coin}}</div>
            <span></span>
          </div>
        </div>
        <div class="text-left margin-tex">
          <div class="ul_title f36 "></div>
          <div class="flex">
            <span ><a class="dot"></a></span>
            <p class="f24">2()</p>
          </div>
        </div>
        <div class="text-left margin-tex">
          <div class="ul_title f36 "></div>
          <div class="flex">
            <span><a class="dot"></a></span>
            <p class="f24 "></p>
          </div>
          <!-- <div class="flex">
            <span><a class="dot"></a></span>
            <p class="f24 color999">App{{coinParams.extra_coin > 0 ? coinParams.extra_coin : ''}}</p>
          </div> -->
        </div>
        <div class="clicks" @click="outVanish"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    coinParams: {
      type: Object,  // coin  type [article, , video , smallVideo  extra_coin ]
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      isShow: true,
      first_tips: ''
    }
  },
  mounted () {
    this.first_tips = this.$utils.cache('first_tips')
  },
  methods: {
    outVanish () {
      this.isShow = false
    }
  }
}
</script>

<style lang="scss" scoped>
  .f44{
    font-size: 44px;
  }
  .article-md{
    position: relative;
  }
  .article-model{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 630px;
    height: 840px;
    background: url("../../assets/img/read_background.png") no-repeat;
    background-size: 100%;
    box-shadow: 0 2px 8px 0 rgba(74,74,74,0.50);
    border-radius: 16px;
    z-index: 100;
    .title{
      height: 165px;
      line-height: 165px;
      color: #fff;
    }
    .cten{
      .img{
        width: 255px;
        height: 142px;
        position: relative;
        margin: 20px auto 44px;
        img{
          height: 100%;
          width: 100%;
        }
        .f36{
          position: absolute;
          font-weight: 900;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -40%);
          color: #AA5200;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-flow: column wrap;
          line-height: 45px;
          span{
            font-weight: normal;
            font-size: 20px;
            line-height: 30px;
          }
        }
      }
      .margin-tex{
        margin: 0 40px;
        .ul_title{
          line-height: 42px;
          margin-bottom: 7px;
          font-weight: 900;
          color: #232323;
          span{
            display: block;
          }
        }
        .flex{
          margin-bottom: 18px;
          color: #959595;
        }
      }
     }
    .dot{
      display: block;
      background: #FFAC00;
      height: 12px;
      width: 12px;
      border-radius: 50%;
      margin-top: 10px;
      margin-right: 14px;
    }
    p{
      line-height: 40px;
      letter-spacing: 2px;
      font-size: 28px;
    }
    .clicks{
      background: #FFAC00;
      width: 400px;
      height: 80px;
      line-height: 80px;
      border-radius: 80px;
      margin: auto;
      color: #fff;
      margin-top: 30px;
    }
}
</style>



