<template>
  <div id="shareBox" class="maskBox" :class="{noQQ: showTreasureBox}" @click="shareAction('cancel')" style="z-index: 15">
    <div class="shareContent" @click.stop="''" :class="{active: active}">
      <div class="shareWay">
        <!--  -->
        <div class="shareFlex flex-row" @click="shareAction('wechat')">
          <div class="iconSize">
            <img src="../../assets/img/my/share_wechat.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!--  -->
        <div class="shareFlex flex-row" @click="shareAction('friend')">
          <div class="iconSize">
            <img src="../../assets/img/my/share_friend.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!-- QQ -->
        <div class="shareFlex flex-row" @click="shareAction('qq')">
          <div class="iconSize">
            <img src="../../assets/img/my/share_qq.png" alt="">
          </div>
          <div class="text">QQ</div>
        </div>
      </div>
      <div class="cancel flex-row" @click="shareAction('cancel')"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    showTreasureBox: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      active: false
    }
  },
  methods: {
    shareAction (action) {
      this.$emit('shareAction', action)
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.active = true
    })
  }
}
</script>
<style lang="scss" scoped>
#shareBox {
  @keyframes moveTo {
    from {
      bottom: -500px;
    }
    to {
      bottom: 24px;
    }
  }
  .shareContent {
    position: absolute;
    width: 697px;
    bottom: 24px;
    height: 0;
    transform: translate3d(-50%, 0, 0);
    overflow: hidden;
    top: auto;
    animation: moveTo 300ms ease;
    .shareWay {
      display: flex;
      justify-content: space-between;
      padding: 67px 40px 54px;
      .shareFlex {
        flex: 1;
        display: flex;
        flex-flow: column;
        justify-content: center;
        .iconSize {
          width: 100px;
          height: 100px;
        }
        .text {
          color: #232323;
          line-height: 45px;
          padding-top: 18px;
        }
      }
    }
    .cancel {
      border-top: 1px solid #dadada;
      font-size: 32px;
      color: #333333;
      height: 84px;
      line-height: 84px;
      text-align: center;
      bottom: 0;
    }
  }
  .shareContent.active {
    height: 370px;
  }
  &.noQQ {
    .shareFlex:nth-child(3){
      display: none;
    }
  }
}
</style>



